.photobook {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    width: 100vw;
    padding: 2vw;
    box-sizing: border-box;
}

.photo-container.split {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: center;
    width: 90vw;
    min-height: 90vh;
    background: var(--color-bg-alt);
    border-radius: 16px;
    box-shadow: 0 2px 12px rgba(0,0,0,0.1);
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
}

.photo-half {
    flex: 1 1 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--color-bg);
    padding: 2vw;
    box-sizing: border-box;
}

.photo-half img {
    max-width: 100%;
    max-height: 70vh;
    object-fit: contain;
    box-shadow: 0 6px 6px rgba(0,0,0,0.5);
    opacity: 1;
    transition: opacity 0.5s cubic-bezier(0.4,0,0.2,1);
}

/* Fade-out class for image transition */
.photo-half img.fade-out {
    opacity: 0;
}

.description-half {
    flex: 1 1 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 4vw 2vw;
    box-sizing: border-box;
}

.caption {
    margin-bottom: 16px;
    word-break: break-word;   
    color: var(--color-text);
    text-align: center;
}

.description {
    word-break: break-word;   
    color: var(--color-text-alt);
}

.close-button {
    position: absolute;
    top: 32px;
    right: 32px;
    z-index: 2;
    background: var(--color-muted);
    color: var(--color-text-alt);
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    font-size: 1.2em;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
    opacity: 0.85;
    transition: background 0.2s, opacity 0.2s;
}

.close-button:hover {
    background: var(--color-accent);
    color: var(--color-text);
    opacity: 1;
}

.nav-button {
    position: absolute;
    top: 95%;
    transform: translateY(-50%);
    z-index: 2;
    background: var(--color-muted);
    color: var(--color-text-alt);
    border: none;
    border-radius: 50%;
    width: 44px;
    height: 44px;
    font-size: 1.5em;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
    opacity: 0.85;
    transition: background 0.2s, opacity 0.2s;
}

.nav-button.left {
    left: 16px;
}

.nav-button.right {
    right: 16px;
}

.nav-button:hover {
    background: var(--color-accent);
    color: var(--color-text);
    opacity: 1;
}

.nav-button:disabled {
    opacity: 0.4;
    cursor: not-allowed;
}

/* Responsive styles for mobile */
@media (max-width: 800px) {
    .photo-container.split {
        flex-direction: column;
        width: 98vw;
        min-height: 50vh;
        max-width: 98vw;
    }
    .photo-half,
    .description-half {
        flex: 1 1 100%;
        width: 100%;
        min-width: 0;
        padding: 4vw 2vw;
    }
    .photo-half img {
        max-height: 40vh;
    }
    .close-button {
        top: 8px;
        right: 8px;
    }
    .nav-button.left,
    .nav-button.right {
        top: unset;
        bottom: 16px;
        transform: none;
    }
    .nav-button.left {
        left: 16px;
    }
    .nav-button.right {
        right: 16px;
    }
}
