/* Color pallete     
    212529 - Dark Steel
    343A40 
    6C757D
    ADB5BD
    CED4DA
    E9ECEF
    F8F9FA - Light Steel
*/

:root {
    /* Light Theme Colors */
    --color-bg: #F8F9FA;
    --color-bg-alt: #E9ECEF;
    --color-text: #212529;
    --color-text-alt: #343A40;
    --color-accent: #6C757D;
    --color-muted: #ADB5BD;
    --color-border: #CED4DA;
    --color-header: #212529;
    --color-footer: #343A40;
}

.light-theme {
    /* Light Theme Colors */
    --color-bg: #F8F9FA;
    --color-bg-alt: #E9ECEF;
    --color-text: #212529;
    --color-text-alt: #343A40;
    --color-accent: #6C757D;
    --color-muted: #ADB5BD;
    --color-border: #CED4DA;
    --color-header: #212529;
    --color-footer: #343A40;
}

.dark-theme {
    /* Dark Theme Colors */
    --color-bg: #212529;
    --color-bg-alt: #343A40;
    --color-text: #F8F9FA;
    --color-text-alt: #E9ECEF;
    --color-accent: #CED4DA;
    --color-muted: #ADB5BD;
    --color-border: #6C757D;
    --color-header: #F8F9FA;
    --color-footer: #E9ECEF;
}

body {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    background-color: var(--color-bg);
    color: var(--color-text);
    transition: background-color 0.3s, color 0.3s;
}

a {
    color: var(--color-accent);
    text-decoration: none;
}

a:hover {
    color: var(--color-text-alt);
    text-decoration: underline;
}

/* Header bar styles */
.header-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 2rem 1rem 2rem;
}

.site-title {
    font-size: 2rem;
    font-weight: bold;
}

h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
    margin-bottom: 2rem;
    font-family: 'Fjalla One', sans-serif;
    font-weight: 300;
}

h1 {
    font-size: 4.0rem;
    line-height: 1.2;
    letter-spacing: -.1rem;
}

h2 {
    font-size: 3.6rem;
    line-height: 1.25;
    letter-spacing: -.1rem;
}

h3 {
    font-size: 3.0rem;
    line-height: 1.3;
    letter-spacing: -.1rem;
}

h4 {
    font-size: 2.4rem;
    line-height: 1.35;
}

h5 {
    font-size: 2.0rem;
    line-height: 1.5;
}

h6 {
    font-size: 1.8rem;
    line-height: 1.6;
}

strong {
    font-weight: 700;
}

p {
    margin: 0 0 2rem;
    font-size: 1.6rem;
    line-height: 1.6;
    color: var(--color-text-alt);
}

.footer {
    font-size: 1.2rem;
    line-height: 1.5;
    color: var(--color-footer);
}

.tag-filter-bar {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-left: 2rem;
    align-items: center;
}

.tag-btn {
    background: var(--color-bg-alt);
    color: var(--color-text);
    border: 1px solid var(--color-border);
    border-radius: 16px;
    padding: 0.3em 1em;
    font-size: 1em;
    cursor: pointer;
    transition: background 0.2s, color 0.2s, border 0.2s;
    outline: none;
}

.tag-btn.selected,
.tag-btn:hover {
    background: var(--color-accent);
    color: #fff;
    border-color: var(--color-accent);
}