.theme-toggle-switch {
    background: var(--color-accent);
    border: none;
    border-radius: 999px;
    width: 56px;
    height: 32px;
    position: relative;
    cursor: pointer;
    outline: none;
    transition: background 0.3s;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 4px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.toggle-thumb {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: var(--color-bg);
    color: var(--color-text-alt);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2em;
    position: absolute;
    top: 4px;
    left: 4px;
    transition: left 0.3s cubic-bezier(0.4,0,0.2,1), background 0.3s;
    box-shadow: 0 1px 4px rgba(0,0,0,0.10);
}

.toggle-thumb.dark {
    left: 28px;
}

.toggle-thumb.light {
    left: 4px;
}

.theme-toggle-switch:active .toggle-thumb {
    box-shadow: 0 2px 8px rgba(0,0,0,0.2);
}
